import React from 'react';
import axios from 'axios';

export default class ApartmentList extends React.Component {
  constructor(props){
    super(props)
    this.state = {
        apartments: []
    }
  }

  componentDidMount() {
    const filter = {
      "conditionList": [
        {
          "logicalOperator": "AND",
          "fieldName": "floorPlanId",
          "operator": "EQUALS",
          "fieldValue": "5"
        },
        {
          "logicalOperator": "AND",
          "fieldName": "floorPlanId",
          "operator": "EQUALS",
          "fieldValue": "5"
        }
      ],
      "orderFields": {
        "unitNumber": true
      }
    };

    const headers = {
      'Accept': 'application/json',
      'x-pmc': 'vista',
      'Content-Type': 'application/json'
    };

    axios.post(`https://k8s.devpv.us/portfolio/api/unit/search/${this.props.buildingId}?withPriceRange=true&page=1&recordsPerPage=25`, { filter }, { headers })
      .then(res => {
        console.log("RESPONSE RECEIVED: ", res);
        const apartments = res.data.records;
        this.setState({ apartments: apartments });
      })
  }

  render() {
    return (
      <ul>
        {
          this.state.apartments
            .map(apartment =>
              <div class="container">
                <div>
                    <div class="col-xs-2">Apartment
                    <div class="detail">#{apartment.unitNumber}</div>
                    </div>
                    <div class="col-xs-2">Sq. Ft.
                    <div class="detail">{apartment.floor} Sq. Ft.</div>
                    </div>
                    <div class="col-xs-2">Price per Month
                    <div class="detail">${apartment.minPrice} - ${apartment.maxPrice}*</div>
                    </div>
                    <div class="col-xs-2"></div>
                    <div class="col-xs-4 button"><button class="gold">Select</button></div>
                </div>
              </div>
            )
        }
      </ul>
    )
  }
}