iumgray-color:#d1d1d1;\n--lightgray-color:#f9f9f9;\n--text-color:#000000;\n--error-color:201, 0, 0;\n--warning-color:231,161,0;\n--success-color:19, 139, 29;\n--input-border-radius:2px;\n--button-border-radius:2px;\n--container-border-radius:6px;\n--body-text-size:14px;\n--caption-text-size:12px;\n}\n\n/* ------------------------Defaults---------------------------------- */\n\n*{\npadding:0px;\nmargin: 0px;\nbox-sizing: border-box;\noutline: none;\nfont-weight: 300;\nfont-size:var(--body-text-size);\nfont-style: normal;\n-webkit-appearance: none;\n-moz-appearance: none;\nappearance: none;\n-webkit-font-smoothing: antialiased;\nfont-family:sans-serif;\nline-height: 1.2em;\ncolor: var(--text-color);\n}\n\nbody,html{height: 100%; padding: 40px;}\n\nul{\n\tlist-style: none;\n}\n\n\n\n/* ------------------------Typography---------------------------------- */\n\nh1,h2,h3,h4{\nclear: both;\nfont-weight: 600;\n}\n\nh1{font-size: 26px;}\n\nh2{font-size: 24px;}\n\nh3{font-size: 18px;}\n\nh4{font-size: 14px;}\n\np{font-size:var(--body-text-size);margin-bottom: 8px;line-height: 1.5em;}\n\np.caption{font-size:var(--caption-text-size);}\n\np.error{\ncolor:var(--error-color);\nfont-size:var(--caption-text-size);\n}\n\n.align_center{text-align:center;}\n.align_right{text-align:right;}\n\nh1 + P,h2 + P,h3 + P,h4 + P{\nmargin-top: 8px;\n}\n\na{\n\ttext-decoration: none;\n\tcolor: var(--primary-color);\n}\n\n\n/* ------------------------Notification---------------------------------- */\n\n\n.notification{\n\tdisplay: block;\n\tborder-radius: var(--container-border-radius);\n\tpadding: 12px;\n  font-size: 12px;\n\tmargin-bottom: 12px;\n}\n\n.notification.min-width{\n\tdisplay: table;\n}\n\n.notification.success{\n\tcolor: rgba(var(--success-color), 1);\n\tbackground-color: rgba(var(--success-color), 0.2);\n}\n\n.notification.warning{\n\tbackground-color: rgba(var(--warning-color), 0.2);\n}\n\n.notification.error{\n\tcolor: rgba(var(--error-color), 1);\n\tbackground-color: rgba(var(--error-color), 0.2);\n}\n\n/* ------------------------Avitar---------------------------------- */\n\n.avitar{\n\tdisplay: block;\n\twidth: 40px;\n\theight: 40px;\n\tbackground-color:var(--primary-color);\n\toverflow: hidden;\n\tborder-radius: 40px;\n\tbackground-repeat: no-repeat;\n\tbackground-image: url(\"../img/avitar.svg\");\n\tbackground-position: center;\n\tbackground-size: 20px;\n}\n\n.avitar.document{\n\tbackground-image: url(\"../img/document.svg\");\n}\n\n.avitar.pet{\n\tbackground-image: url(\"../img/pet.svg\");\n}\n\n\n/* -----------------------------------PV---------------------------------- */\n\n\n#PV_Wrapper{\ndisplay: flex;\nflex-direction: column;\nposition: relative;\nwidth: 100%;\nheight: auto;\n}\n\n#PV_Header{\ndisplay: flex;\nflex-direction: row;\nposition: relative;\nwidth: 100%;\nheight: auto;\njustify-content: space-between;\n}\n\n#PV_Body{\npadding-top: 40px;\n}\n\n#PV_PageTitle{\nmargin-bottom: 32px;\n}\n\n#PV_PageTitle.icon_secure{\nbackground-repeat: no-repeat;\nbackground-image: url(\"../img/secure.svg\");\nbackground-position: right top;\nbackground-size: 70px;\n}\n\n#PV_PageContent{\ndisplay: flex;\nflex-direction: row;\nposition: relative;\nwidth: 100%;\nheight: auto;\n}\n\n#PV_PageContent #PV_PageContent_Main{\nflex-shrink: 1;\nflex-grow: 1;\n}\n\n#PV_PageContent #PageContent_Right{\nflex-shrink: 0;\nflex-grow: 0;\nwidth: 300px;\nmargin-left: 40px;\n}\n\n\n\n\n\n\n/* ------------------------Form Elements---------------------------------- */\n\nlabel{\nfont-size: 12px;\ndisplay:inline-block;\nfont-weight: 300;\nbackground-color: white;\nmargin-left: 10px;\npadding: 0px 8px;\n}\n\ninput[type=text], select , textarea {\nwidth: 100%;\nborder-color:var(--mediumgray-color);\nborder-radius:var(--input-border-radius);\nborder-style: solid;\nborder-width: 1px;\ndisplay: block;\npadding: 16px 16px;\nbackground-color: white;\nheight: 54px;\nfont-weight: 600;\nmargin-bottom: 8px;\nbackground-size: 18px;\nbackground-position: center right 16px;\nbackground-repeat: no-repeat;\n}\n\nselect{\ncursor: pointer;\nbackground-image: url(\"../img/chevron_down.svg\");\n}\n\ninput[type=text].icon_datepicker{\ncursor: pointer;\nbackground-image: url(\"../img/calendar.svg\");\n}\n\ninput[type=text]:focus, select:focus, textarea:focus {border-color:var(--primary-color);}\n\nlabel + input[type=text],\nlabel + select,\nlabel + textarea{\nmargin-top:-11px;\n}\n\ntextarea{\n\tresize:none;\n\theight: auto;\n}\n\ninput[type=text]:disabled,select:disabled{\nbackground-color:var(--lightgray-color);\nborder-color:var(--mediumgray-color);\ncolor:var(--mediumgray-color);\ncursor:not-allowed;\n}\n\ninput[type=radio]{\nwidth: 18px;\nheight: 18px;\nborder-color:var(--mediumgray-color);\nborder-radius:100%;\nborder-style: solid;\nborder-width: 1px;\ncursor: pointer;\n}\n\ninput[type=radio]:checked{\nborder-color:var(--primary-color);\nborder-width: 6px;\n}\n\n\ninput[type=checkbox]{\nwidth: 18px;\nheight: 18px;\nborder-color:var(--mediumgray-color);\nborder-radius:2px;\nborder-style: solid;\nborder-width: 1px;\ncursor: pointer;\n}\n\ninput[type=checkbox]:checked{\nbackground-color:var(--primary-color);\nborder-color:var(--primary-color);\nbackground-repeat: no-repeat;\nbackground-image: url(\"../img/checkmark_white.svg\");\nbackground-position: center;\nbackground-size: 10px;\n}\n\ninput[type=checkbox]:disabled,input[type=radio]:disabled{\nborder-color:var(--mediumgray-color);\nbackground-color:var(--lightgray-color);\ncursor:not-allowed;\n}\n\ninput.error{\nborder-color:var(--error-color)!important;\n}\n\n\n\n/* ------------------------Layouts---------------------------------- */\n\n\n.layout_select_list_grouping{\ndisplay: flex;\nflex-direction:row;\nalign-items: stretch;\n}\n\n.layout_select_list_grouping .select_item{\nflex-shrink: 0;\npadding-right: 24px;\npadding-top: 2px;\ndisplay: flex;\n}\n\n.layout_select_list_grouping .select_item.vertical_center{\n align-items: center;\n}\n\n.layout_select_list_grouping .content{\nflex-grow: