import React, { Component } from "react";
import './css/pv.css';
import ApartmentList from "./components/ApartmentList";
import PropTypes from "prop-types";
const { string } = PropTypes;

class App extends Component {

  static propTypes = {
      buildingId: string,
  };

  static defaultProps = {
      buildingId: "2"
  };

  render() {
    return (
      <div className="App">
        <ApartmentList buildingId={this.props.buildingId} />
      </div>
    );
  }
}

export default App;
